#modal {
  position: fixed;
  z-index: -999;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(16, 39, 62, 0.85);
  opacity: 0;
  transition: 0.4s cubic-bezier(0.39, 0.575, 0.565, 1);
}

#modal.active {
  opacity: 1;
  z-index: 999;
}

#modal.active .window {
  transform: scale(1) translate(-50%, -50%);
  opacity: 1;
}

#modal .window {
  transform: scale(0.5) translate(-50%, -50%);
  opacity: .5;
  position: absolute;
  z-index: 9999;
  top: 50%;
  left: 50%;
  padding: 3rem 1rem;
  background-color: #fff;
  border-top: 8rem solid #214b75;
  border-bottom: 8rem solid #214b75;
  border-left: 9rem solid #214b75;
  border-right: 9rem solid #214b75;
  box-sizing: border-box;
  transform-origin: 0 0;
  transition: 0.4s cubic-bezier(0.39, 0.575, 0.565, 1);
  max-height: 90%;
}

#modal .window .close {
  position: absolute;
  width: 9rem;
  height: 8rem;
  bottom: 100%;
  left: 100%;
  font-size: 0;
  line-height: 0;
  text-indent: -9999px;
  background-size: auto;
  background-repeat: no-repeat;
  background-image: url(../images/ico-close.min@sm.png);
  background-position: center center;
  cursor: pointer;
  transition: 0.2s cubic-bezier(0.39, 0.575, 0.565, 1);
}

@media all and (min-width: 992px) {
  #modal .window .close {
    background-image: url(../images/ico-close.min.png);
  }
}

#modal .window .close:hover {
  transform: scale(1.1);
}

#modal .window p {
  font-size: 2.5rem;
  font-weight: 700;
  color: #214b75;
  margin-bottom: 3rem;
  text-align: center;
}

#modal .window ul {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

#modal .window ul li {
  position: relative;
  font-size: 0;
  line-height: 0;
  text-indent: -9999px;
  margin: 0.6rem;
  flex-grow: 0;
  max-width: 40%;
  transition: 0.2s cubic-bezier(0.39, 0.575, 0.565, 1);
}

#modal .window ul li a {
  position: relative;
  display: block;
  width: 13.5rem;
  height: 9.5rem;
  max-width: 100%;
  max-height: 12vh;
  transition: 0.2s cubic-bezier(0.39, 0.575, 0.565, 1);
}

